import { Component, OnInit } from "@angular/core";
import { IncomeService } from "../services/income.service";
import { Router, ActivatedRoute } from "@angular/router";

import { NgbActiveModal } from "@ng-bootstrap/ng-bootstrap";

import swal from "sweetalert2";

@Component({
  selector: "app-add-income",
  templateUrl: "./add-income.component.html",
  styleUrls: ["./add-income.component.scss"]
})
export class AddIncomeComponent implements OnInit {
  newincome;
  submitting: boolean;
  constructor(
    public incomeservice: IncomeService,
    public router: Router,
    public activeRouter: ActivatedRoute,
    public activeModal: NgbActiveModal
  ) {
    this.newincome = {};
  }

  ngOnInit() {
    this.incomeservice.getApplicants().subscribe((data: any) => {
      console.log("data====>", data)
    }, (err) => {
      console.log(err)
    })
  }

  closeModal() {
    this.activeModal.close("Modal Closed");
  }

  submit() {
    console.log("Submitted>>>>>>>>>", this.newincome);

    swal
      .fire({
        title: "Do you want to add the Applicant ?",
        text: "New user will be added in Applicant List",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Add"
      })
      .then(result => {
        if (result.value) {
          //dev

          this.incomeservice.addIncome(this.newincome).subscribe(
            (data: any) => {
              swal.fire("Add!", "Applicant has been added.", "success").then(() => {
                this.router.navigate(['account/income']);
              })
              //dev
              console.log("Dataaa changeddd", data);
            },
            error => {
              swal.fire("Not Added!", "There was some error.", "error");
              //dev
              console.log("ERRORR", error);
            }
          );
        }
      });
  }
}
