import { Component, OnInit } from "@angular/core";
import { NgbModal, NgbActiveModal } from "@ng-bootstrap/ng-bootstrap";
import { AddIncomeComponent } from "./add-income/add-income.component";
import { PaymentComponent } from "./payment/payment.component";
import { Router, ActivatedRoute } from "@angular/router";
import swal from "sweetalert2";
@Component({
  selector: "app-income",
  templateUrl: "./income.component.html",
  styleUrls: ["./income.component.scss"]
})
export class IncomeComponent implements OnInit {
  incomeService: any;

  constructor(
    private modalService: NgbModal,
    public activeModal: NgbActiveModal,
    public router: Router,
    public activeRouter: ActivatedRoute
  ) {}

  makepayment() {
    const modalRef = this.modalService.open(PaymentComponent, {
      windowClass: "dark-modal",
      backdropClass: "light-blue-backdrop"
    });
    modalRef.result
      .then(result => {
        console.log(result);
      })
      .catch(error => {
        console.log(error);
      });
  }

  delete(id) {
    swal
      .fire({
        title: "Do you want to delete the income ?",
        text: "You won't be able to revert the deleted income",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Delete"
      })
      .then(result => {
        if (result.value) {
          //dev

          this.incomeService.deleteIncome(id).subscribe(
            (data: any) => {
              swal.fire("Deleted!", "Income has been deleted.", "success");
              //dev
              console.log("Dataaa changeddd", data);
              this.ngOnInit();
            },
            error => {
              swal.fire("Not Changed!", "There was some error.", "error");
              //dev
              console.log("ERRORR", error);
            }
          );
        }
      });
  }

  ngOnInit() {}
}
